.templates {
  padding-top: 6.4rem;
  background-color: #fff;
  &__title {
    font-size: 3rem;
    margin-bottom: 1.2rem;
    line-height: 1.4;
  }
  &__subtitle {
    font-size: 2.4rem;
    line-height: 1.58;
  }
  &__header {
    padding-bottom: 50px;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
  .teasers {
    padding-bottom: 72px;
    .teaser {
      &:not(:last-child) {
        margin-bottom: 40px;
      }
      &__title {
        margin-bottom: 12px;
      }
      &__inner {
        display: flex;
        align-items: flex-start;
      }
      &__list {
        display: flex;
        column-gap: 16px;
      }
      .item {
        text-align: center;
        button {
          margin-top: -10px;
        }
      }
      &--adoption {
        .all-collection-card {
          background-image: url("https://c.bonfireassets.com/static/design-template-category/a45d2427-8e00-424f-ba06-394692991256/afb2667e45124ce5b3007cca333fd5e8/card-templates-adoption.jpg");
          background-color: rgb(249, 195, 159);
        }
      }
      &--events {
        .all-collection-card {
          background-image: url("https://c.bonfireassets.com/static/design-template-category/e6d642a1-7a0a-448b-abdc-0c5e12b01159/863a99db59a64c01ae1ba695cd2367ac/tpl-rb.jpg");
          background-color: rgb(235, 235, 243);
        }
      }
    }
    .all-collection-card {
      margin-left: 20px;
      min-width: 214px;
      min-height: 288px;
      padding: 24px 16px;
      border-radius: 8px;
      background-repeat: no-repeat;
      background-position: right bottom;
      background-size: 18rem;

      span {
        &::after {
          content: "";
          display: inline-block;
          background-image: url("../../img/arrow_right.svg");
          background-size: contain;
          background-repeat: no-repeat;
          vertical-align: middle;
          margin-left: 5px;
          width: 13px;
          height: 20px;
        }
      }
    }
  }
  .category {
    flex: 1;
    &-list {
      display: flex;
      flex-wrap: wrap;
    }
    &-item {
      flex-basis: 20%;
    }
    &-link {
      width: 100%;
      font-size: 1.5rem;
      line-height: 2;
      color: #5e82a6;
      &:hover {
        color: #69aaec;
      }
    }
  }
  .guide-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2.4rem 3.6rem;
    border-radius: 8px;
    flex: 1;
    max-width: 56rem;
    background: url("https://c.bonfireassets.com/images/templates4.jpg") right -4rem
      center no-repeat #f7f6f4;
    background-position: 56% center;
    background-size: 19rem;
    &__title {
      max-width: 16rem;
      font-size: 2rem;
      font-weight: 400;
    }
  }
  .design-tips {
    &__title {
      text-align: center;
      margin-bottom: 3.6rem;
    }
    .design-tip {
      &__number {
        text-transform: uppercase;
      }
      &--1 {
        background-color: $color-primary;
        color: #fff;
        display: flex;
        align-items: center;
        p {
          color: $color-secondary;
        }
        .content-box {
          text-align: left;
          margin-left: auto;
          margin-right: 10rem;
        }
        &__bg {
          width: 60%;
          min-height: 60rem;
          background-image: url("https://c.bonfireassets.com/images/templates1.jpg");
          background-position: center top;
          background-repeat: no-repeat;
          background-size: cover;
        }
      }
      &--2 {
        text-align: center;
        padding: 10rem;
      }
      &--3 {
        padding: 14.4rem 9.6rem;
        background-image: url("https://c.bonfireassets.com/images/templates3@2x.jpg"),
          url("https://c.bonfireassets.com/images/templates3-2@2x.jpg");
        background-size: contain;
        background-repeat: no-repeat, repeat-x;
        background-position: left center, left top;

        &__content {
          max-width: 48rem;
          padding: 6.4rem 4.8rem;
          margin-left: auto;
          background-color: #fff;
        }
      }
      &--4 {
        background-color: #f8f4f5;
        padding: 96px 0 48px;
        &__inner {
          display: flex;
          align-items: center;
        }
      }
    }
  }
}
.title-wrap {
  margin-bottom: 7.2rem;
}
