.storySlider {
  background-color: #f1f4f8;
  text-align: center;
  padding: 9.6rem;
  .carousel {
    position: relative;
    max-width: 90rem;
    min-height: 40rem;
    margin: 0 auto;
  }
  .slider {
  }
  .slide {
    background-color: #fff;
    position: absolute;
    max-width: 80rem;
    display: flex;
    margin-left: 5rem;
    transition: all 0.3s;
    opacity: 0;
    &--active {
      border-color: transparent;
      z-index: 10;
      opacity: 1;
    }
    &:nth-child(2) {
      top: 0.5rem;
      left: 0.5rem;
    }
    &:nth-child(3) {
      top: 1rem;
      left: 1rem;
    }
    img {
      width: 32rem;
    }
    &__text {
      max-width: 48rem;
      text-align: left;
      padding: 6.4rem 4.8rem;
    }
  }
  .controls {
    .arrow {
      position: absolute;
      cursor: pointer;
      top: 27%;
      i {
        font-size: 8rem;
      }
      &--left {
        left: -1rem;
        transform: scale(-1);
      }
      &--right {
        right: -1rem;
      }
    }
  }
}
