.oshirt {
  padding: 15rem 0;
  background: #f7f6f4;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 1rem;
  }
  &__title {
    &::after {
      content: "";
      background-image: url("../../img/supply-logo.svg");
      background-position: right;
      background-repeat: no-repeat;
      vertical-align: middle;
      width: 9.6rem;
      height: 2.6rem;
      display: inline-block;
      margin-left: 1.2rem;
      border-left: solid 1px #bdcad8;
    }
  }
  &__bg {
    background-image: url("../../img/order_shirt_background.jpg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: 77rem;
    height: 44rem;
  }
  &__info {
    max-width: 38.4rem;
  }
  &__gift {
    display: flex;
    column-gap: 1rem;
  }
}
