.contact {
  padding: 160px 0 95px;
  text-align: center;
  .socials {
    &__title {
      margin-bottom: 27px;
      font-size: 40px;
      line-height: 57px;
      text-transform: capitalize;
      letter-spacing: 0.2px;
      font-weight: 400;
    }
    &__text {
      margin-bottom: 95px;
    }
    .social-list {
      display: flex;
      align-items: center;
      justify-content: center;
      column-gap: 54px;
    }
  }

  .contacts {
    &__inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .contact-list {
      padding: 0 13px;
    }
    .contact-list-item {
      text-align: center;
      max-width: 220px;

      &:not(:last-child) {
        margin-bottom: 75px;
      }

      img {
        margin: 0 auto;
        margin-bottom: 20px;
      }
    }
  }
  .map {
    padding: 95px 0 143px;
  }
  .form {
    background-color: #fff;
    padding: 52px 80px;
    border-radius: 8px;
    box-shadow: 3px 3px 30px #000;
    flex: 1;
    max-width: 880px;
    &__title {
      margin-bottom: 45px;
    }
    &__btn {
      display: block;
    }

    input {
      min-height: 64px;
    }
    input,
    textarea {
      width: 100%;
      border: 1px solid #e5e5e5;
      border-radius: 10px;
      padding: 25px 21px;
      background: #f5f5f5;
      margin-bottom: 45px;
    }

    textarea {
      resize: none;
      min-height: 193px;
    }
  }
}
