.ctlg {
  background-color: #fff;
  .ctlg-hero {
    padding-top: 12rem;
    .ctlg-grid {
      display: grid;
      grid-gap: 4.8rem;
      &--hero {
        grid-template-columns: repeat(2, 1fr);
        margin-bottom: 4.8rem;
      }
      &--home {
        grid-template-columns: repeat(3, 1fr);
        margin-bottom: 7.2rem;
      }
      .link {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100%;

        &__title {
          margin-bottom: 16px;
        }
      }
      &__item {
        border-radius: 8px;
        transition: box-shadow 0.3s;
        box-shadow: inset 0 0 0 2px #f1f4f8, 0 0 0 0 #f1f4f8;
        padding: 24px 0;
        &--full {
          padding: 0;
          grid-column: 1 / -1;
        }
        &--half {
          padding: 0 72px;
          background-size: 40rem;
          background-repeat: no-repeat;
          min-height: 40rem;
          background-position: right center;
          .link {
            align-items: flex-start;
            justify-content: center;
          }
          img {
            max-width: 28.7rem;
          }
          &:nth-child(2) {
            background-color: #f1f4f8;
            background-image: url("https://c.bonfireassets.com/images/catalog/hero-longsleeve.jpg");
          }
          &:nth-child(3) {
            background-color: #f7f6f4;
            background-image: url("https://c.bonfireassets.com/images/catalog/hero-crewneck.jpg");
          }
          &:nth-child(4),
          &:nth-child(5) {
            .link {
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
            }
          }
        }
        &:hover {
          box-shadow: inset 0 0 0 0 #f1f4f8, 5px 5px 25px 0 #f1f4f8;
        }
      }
      &__img {
        max-width: 20rem;
      }
    }
    .showcase-link {
      width: 100%;
      padding: 24px 0;
      border-radius: 8px;
      background-image: url("https://c.bonfireassets.com/images/catalog/hero-shirts.jpg");
      background-repeat: no-repeat;
      background-color: #efe7e3;
      background-position: bottom -3.5rem right 0;
      background-size: 43.5rem;
      min-height: 55rem;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      &__title {
        font-size: 8.4rem;
        line-height: 126px;
        text-transform: uppercase;
        letter-spacing: 2rem;
        color: #fff;
      }
      &__subtitle {
        font-size: 2rem;
        line-height: 32px;
        margin: 1.2rem 0 4.8rem;
        font-weight: 400;
      }
    }
  }
  .feedback {
    text-align: center;
    padding-top: 0;
    padding-bottom: 7.2rem;

    &__title {
      font-size: 2rem;
      font-weight: 400;
      line-height: 1.6;
      margin-bottom: 24px;
    }
  }
  .designing {
    padding: 137px 24px;
    background-image: url("../../img/designing-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right center;
    &__content {
      margin-left: auto;
      padding: 64px 48px;
      max-width: 430px;
    }
    &__title {
      font-weight: 400;
    }
  }
}
.navigation {
  margin-bottom: 3.6rem;
  a {
    &::after {
      display: inline-block;
      content: "/";
      margin-left: 0.5rem;
      margin-right: 0.5rem;
    }
  }
  span {
    border-bottom: 1px solid #000;
    padding-bottom: 2px;
  }
}
