.trate {
  &__inner {
    padding: 7rem 0;
  }
  &__title {
    font-weight: 400;
    line-height: 1.58;
    max-width: 51rem;
    text-align: center;
    margin: 0 auto 6.5rem;
  }
  &__list {
    display: flex;
    column-gap: 7.5rem;
  }
  &__item {
    flex: 1;
    outline: 2px solid #ebeff3;
    outline-offset: 2rem;
    transition: all 0.3s;
    &:hover {
      transform: scale(1.05) translateY(-1rem);
      outline-offset: 0;
      outline-color: transparent;
    }
  }
}
