.cart {
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    visibility: visible;
    .cart__inner {
      transform: scale(1) translate(-50%, 15%);
    }
    .cart__wrapper {
      background-color: rgba($color: #000, $alpha: 0.4);
    }
  }
  &__inner {
    transition: transform 0.3s;
    min-width: 700px;
    position: fixed;
    top: 10%;
    left: 50%;
    transform: translateX(-50%) scale(0.8);
    background: #ffffff;
    border-radius: 8px;
    padding: 50px 40px;
  }
  &-list {
    margin-bottom: 20px;
  }
  .element {
    display: flex;
    column-gap: 15px;
    &__delete-btn {
      align-self: center;
      margin-left: auto;
      background-color: $color-secondary;
      width: 35px;
      height: 35px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      font-size: 2.5rem;
      cursor: pointer;
    }
    .qty {
      input {
        width: 40px;
        height: 40px;
        border-radius: 8px;
        background-color: $color-secondary;
      }
    }
  }
  &__add-another-item {
    display: block;
    width: max-content;
    margin-left: auto !important;
  }
  &__wrapper {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    transition: background-color 0.3s;
    background-color: transparent;
  }
  &__cancel-btn {
    transform: scale(2);
    position: absolute;
    right: 50px;
    top: 20px;
    cursor: pointer;
  }
}
