.feedback {
  padding: 2rem 0;
  &__content {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 5.7rem;
  }

  &__feedback {
    background-image: url("../../img/color-2.png");
    background-position: center;
    background-size: cover;
    max-width: 49rem;
    height: 13.4rem;
    text-align: center;
    display: flex;
    align-items: center;
  }

  .author {
    display: flex;
    align-items: center;

    &__img {
      margin-right: 2.4rem;
      border-radius: 10rem;
    }

    &__name {
      margin-bottom: 8px;
      font-size: 1.6rem;
    }
  }
}
