.homepage {
  .hero {
    background-image: url("../../img/hero_background.jpg");
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: 189.2rem 52.5rem;
    padding-top: 16.5rem;
    background-color: #fff;
    &__content {
      max-width: 36rem;
      margin: 0 auto;
      text-align: center;
    }
    &__slogan {
      font-size: 3rem;
      font-weight: 700;
      margin-bottom: 4rem;
    }
    &__search {
      background-color: #fff;
      box-shadow: -8px -1rem 3.6rem rgba(61, 72, 83, 0.6);
      border-radius: 5px;
      padding: 2.4rem 4.5rem;
      display: flex;
      align-items: center;
      min-height: 11.5rem;
      margin-top: 23rem;
      transform: translateY(1rem);
    }
    .input-field {
      display: flex;
      flex-direction: column-reverse;
      flex: 1;
      margin-left: 3rem;
      &__label {
        text-transform: uppercase;
        font-size: 1.2rem;
        backface-visibility: hidden;
        font-weight: 700;
        letter-spacing: 2px;
        transition: all 0.4s;
        transform: translateY(-5px);
      }
      &__input {
        font-size: 2.4rem;
        color: $color-primary;
        padding: 5px 2.5rem 5px 0;
        transition: all 0.4s;
      }
      &__input:placeholder-shown + label {
        opacity: 0;
        visibility: hidden;
        transform: translateY(20px);
      }
      &__input:placeholder-shown {
        transform: translateY(-7px);
      }
    }
  }
  .picks {
    padding: 6.6rem 0 9.6rem;
    background-color: #fff;
    &__inner {
      text-align: center;
    }
    &__subtitle {
      text-transform: uppercase;
      margin-bottom: 2.4rem;
    }
    &__list {
      display: flex;
      justify-content: center;
      column-gap: 48px;
      margin-bottom: 70px;
    }
  }
  .catalog {
    background-color: #fff;
    &__inner {
      display: flex;
      flex-wrap: wrap;
      column-gap: 36px;
      padding-bottom: 72px;
      & > * {
        flex: 1;
        min-height: 238px;
        &:first-child {
          background-image: url("../../img/catalog-thumb-homepage.jpg");
          background-color: #efe7e3;
          &:hover {
            box-shadow: inset 0 0 0 2px #dfc7b2;
          }
        }
        &:last-child {
          background-image: url("../../img/card-templates-personal.jpg");
          background-color: #f1f4f8;
          &:hover {
            box-shadow: inset 0 0 0 2px $color-secondary;
          }
        }
      }
    }
  }
}
