.store {
  background-color: #fff;
  &__inner {
    display: flex;
    padding: 10rem 0;
  }
  &__content {
    max-width: 37rem;
    margin-right: 8rem;
    margin-left: 320px / 1900px * 100%;
  }
  &__bg {
    background-image: url("../../img/open_store.jpg");
    background-size: contain;
    background-position: left 4.8rem center;
    min-height: 40rem;
    background-size: 90rem;
    background-repeat: no-repeat;
    flex: 1;
  }
  &__btn {
    display: flex;
    align-items: center;
    width: max-content;
    column-gap: 1rem;
  }
}
