.user_profile {
  &_header {
    font-size: 20px;
    text-align: center;
    padding: 14px 16px;
  }
  &_href {
    font-size: 16px;
    color: #5e82a6;
    cursor: pointer;
    padding: 14px 16px;
    width: 100%;
    &:hover {
      background-color: #f1f4f8;
    }
  }
}
.header {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 3.2rem;
  background-color: #fff;
  transition: padding-top 0.3s;
  backface-visibility: hidden;
  &--minimized {
    padding-top: 0;
    border-bottom: 2px solid #f1f4f8;
    background-color: #fff !important;
  }
  &__inner {
    display: flex;
    align-items: center;
    min-height: 6.4rem;
  }
  .menu {
    margin-left: auto;
    &__list {
      display: flex;
      column-gap: 3.2rem;
    }
    &__item {
      position: relative;
      display: flex;
      align-items: center;
      column-gap: 8px;
      cursor: pointer;
      &:hover .submenu {
        opacity: 1;
        visibility: visible;
        top: 20px;
      }
    }
  }
  .submenu {
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;
    position: absolute;
    left: -39px;
    width: 37rem;
    top: 16px;
    padding-top: 8px;
  }
  .submenu-list {
    display: flex;
    flex-direction: column;
    background: #fff;
    box-shadow: 8px 12px 36px 0 rgba(61, 72, 83, 0.21);
    border-radius: 5px;
    padding: 24px 16px;
  }
  .sub-link {
    display: flex;
    align-items: center;
    padding-left: 8px;
    min-height: 50px;
    &__icon {
      &__container{
        border-radius: 50%;
        width: 40px;
        background-color: #efefef;
        height: 40px;
        padding: 7px;
        display: flex;
        align-items: center;
        justify-content: center;

      }
    }
    &__title {
      font-size: 16px;
      margin-left: 8px;
    }
    &__arrow {
      margin-left: auto;
      width: 16px;
      height: 16px;
      transition: transform 0.3s;
    }
    &:hover .sub-link__arrow {
      transform: translateX(4px);
    }
  }
  .user,
  .cart-btn {
    display: flex;
    align-items: center;
    column-gap: 8px;
    margin-left: 10px;
    cursor: pointer;
  }
  .user {
    margin-left: 40px;
    &__img {
      width: 25px;
      height: 25px;
      border-radius: 50%;
    }
  }
  &__login-btn {
    margin-left: 3.2rem;
    margin-right: 1.1rem;
    box-shadow: inset 0 0 0 0 $color-secondary, 5px 5px 25px 0 #f1f4f8;
    &:hover {
      box-shadow: inset 0 0 0 2px $color-secondary, 0 0 0 0 #f1f4f8;
    }
  }
}
