.support {
  background-color: $color-primary;

  &__container {
    display: flex;
    align-items: center;
  }

  &__content {
    color: #fff;
    margin-left: calc(100% / 6);
    max-width: 38.4rem;
  }

  &__bg {
    max-width: 115.2rem;
    max-width: 45%;
    margin-left: auto;
  }
}
