.fundraising {
  padding-top: 6.5rem;
  .header,
  .fhero {
    background-color: #f8f4f5;
  }
  .fhero {
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      vertical-align: bottom;
      min-height: 71rem;
    }
    &__content {
      margin-bottom: 14.2rem;
      a {
        margin-right: 2.4rem;
      }
    }
    &__images {
      display: grid;
      grid-template-columns: 37rem 19rem;
      grid-gap: 3.6rem;
      min-height: 50.6rem;
      transform: translateY(4.6rem);
    }
    &__img {
      &--1 {
        grid-row: 1/4;
      }
      &--2 {
        grid-row: 2/3;
      }
      &--3 {
        grid-row: 3/4;
      }
    }
  }
  .features {
    background-color: #fff;
    text-align: center;
    padding-top: 9.6rem;
    &__title {
      font-weight: 400;
      max-width: 52rem;
      margin-left: auto;
      margin-right: auto;
    }
    .menu {
      margin-bottom: 3.6rem;
      &__list {
        display: flex;
        justify-content: center;
        column-gap: 2.4rem;
      }
      &__link:link,
      &__link:visited {
        color: #5e82a6;
      }

      &__link:active {
        color: $color-primary;
        border-bottom: 2px solid #f1f4f8;
        padding-bottom: 4px;
      }
      &__link:hover {
        color: $color-primary;
      }
    }
    @import url("https://fonts.googleapis.com/icon?family=Material+Icons");
    .carousel {
      position: relative;
      overflow: hidden;
      max-width: 90rem;
      margin: 0 auto;
    }
    .slider {
      height: 100%;
      width: 500%;
      min-height: 40rem;
      display: flex;
      transition: transform 0.3s;
    }
    .slide {
      flex: 1;
      max-width: 80rem;
      margin: 0 5rem;
      &__text {
        max-width: 48rem;
        margin: 1.6rem auto 0;
      }
    }
    .controls {
      .arrow {
        position: absolute;
        top: 30%;
        cursor: pointer;
        i {
          font-size: 8rem;
        }
        &--left {
          transform: scale(-1);
          left: 0;
        }
        &--right {
          right: 0;
        }
      }
    }
  }
  .howItWorks {
    padding: 9.6rem 0 7.2rem;
    text-align: center;
    background-image: url("../../img/cloud.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center 8%;
    &__list {
      display: flex;
    }
    &__item {
      h3 {
        margin-top: 2rem;
      }
      img {
        margin: 0 auto;
      }
    }
  }
  .started {
    color: #fff;
    background-color: $color-primary;
  }
  .communities {
    text-align: center;
    padding: 9.6rem 0 1.6rem;
    background-image: url("../../img/circle-bg.jpg");
    background-repeat: no-repeat;
    background-position: center 10rem;
    background-size: 75rem;
    background-color: #fff;
    &__list {
      display: flex;
      flex-wrap: wrap;
      row-gap: 4.5rem;

      & > *:first-child {
        margin: 0 auto;
      }
    }
    .row {
      flex-basis: 100%;
      display: flex;
      justify-content: center;

      & > *:first-child {
        margin-right: 9.6rem;
      }
    }

    &__item {
      max-width: 38.4rem;
      margin-bottom: 4.8rem;

      img {
        display: inline-block;
        width: 9.6rem;
        border-radius: 50%;
        margin-bottom: 2.4rem;
      }
    }
  }
  .fundraisingType {
    background-color: #fff;
    &__inner {
      display: flex;
      justify-content: center;
      column-gap: 9.6rem;
      padding-bottom: 9.6rem;
    }
    .cart {
      background-size: cover;
      width: 57.6rem;
      height: 36.4rem;
      position: relative;
      &__content {
        position: absolute;
        margin-left: 0;
        bottom: 0;
        padding: 2.7rem 2.2rem 2.4rem;
      }
      &__title,
      &__subtitle {
        font-weight: 800;
      }
      &__title {
        font-size: 1.2rem;
        text-transform: uppercase;
        &::after {
          display: none;
        }
      }
      &__subtitle {
        font-size: 1.6rem;
        &::after {
          content: " ";
          background-image: url("../../img/arrow_right.svg");
          background-repeat: no-repeat;
          vertical-align: middle;
          background-position: center;
          display: inline-block;
          width: 2rem;
          height: 2rem;
          margin-left: 0.5rem;
          transition: transform 0.2s;
        }
      }
      &:hover &__subtitle {
        font-size: 5rem;
      }
      &:first-child {
        background-image: url("https://c.bonfireassets.com/images/link-adoption.jpg");
      }
      &:last-child {
        background-image: url("https://c.bonfireassets.com/images/link-petrescue.jpg");
      }
    }
  }
  .press {
    background-color: $color-primary;
    clip-path: polygon(0 0, 100% 0, 100% 65%, 0 100%);
    &__inner {
      padding: 4.8rem 0 11.2rem;
    }
    &__title {
      color: #fff;
    }
  }
  .combined {
    background-color: #f1f4f8;
    margin-top: -10rem;
    padding-top: 10rem;
  }
  .startfund {
    &__inner {
      padding: 6.4rem 0 9.6rem;
      text-align: center;
    }
  }
  .infoRow {
    display: flex;
    justify-content: center;
    column-gap: 9.6rem;
    padding-bottom: 7rem;
  }
  .occasion {
    background-color: #fff;
    text-align: center;
    &__inner {
      padding: 7.2rem 0;
    }
    &__title {
      max-width: 43rem;
      margin: 0 auto;
    }
    &__list {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      margin: 7rem 24.8rem;
      column-gap: 8.2rem;
      row-gap: 2.7rem;
    }
    .link {
      &:hover img {
        transform: scale(1.1);
      }
      img {
        margin: 0 auto;
        transition: transform 0.3s;
      }
      p {
        margin-top: 8px;
      }
    }
  }
}
