.footer {
  background-color: $color-primary;
  color: #fff;

  &__inner {
    padding-top: 6.4rem;
    padding-bottom: 6.4rem;
    display: flex;
    align-items: flex-start;

    & > * {
      flex: 1;
    }
  }
  &__block-title {
    text-transform: uppercase;
    margin-bottom: 3.6rem;
    font-size: 1.4rem;
    font-weight: 600;
    color: #bdcad8;
    letter-spacing: 2px;
  }
  &__item {
    &:not(:last-child) {
      margin-bottom: 1.6rem;
    }
    &--highlighted {
      background: #455260;
      padding: 8px 16px 8px 8px;
      border-radius: 0px 8px 8px 0px;
      line-height: 20px;
      margin-left: -10px;
      display: inline-block;
      border-left: 2px solid #f49979;

      strong {
        display: block;
      }
    }
  }
  &__link {
    transition: color 0.1s;
    &:hover {
      color: #bdcad8;
    }
  }
  &__giftBtn {
    background: #455260;
    padding: 8px;
    padding-right: 1.6rem;
    border-radius: 8px;
    margin-left: -8px;

    &::before {
      content: "";
      background-image: url("../../img/gift-box.svg");
      display: inline-block;
      width: 2rem;
      height: 2rem;
      margin-right: 1rem;
      vertical-align: top;
    }
  }
  &__social-list {
    display: flex;
    align-items: center;
    column-gap: 2.2rem;
  }
  &__slogan {
    max-width: 180px;
    margin-top: 2.4rem;
    color: #bdcad8;
  }
  &__copyright {
    margin-top: 100%;
    font-size: 1.2rem;
  }
  &__copyright-text {
    opacity: 0.3;
    margin-bottom: 8px;
  }
  &__dmca {
    margin-right: 8px;
  }
  &__campaign-btn {
    margin-top: 3rem;
  }
}
