.sell {
  background-color: #f1f4f8;
  display: flex;
  align-items: center;
  column-gap: 8rem;
  height: 60rem;
  &__content {
    max-width: 374px;
    margin-left: 320px / 1900px * 100%;
  }
  &__heading {
    line-height: 38px;
    max-width: 312px;
  }
  &__text {
    line-height: 25px;
  }
  &__bg-img {
    margin-bottom: -11px;
  }
}
