%btn {
  border-radius: 5px;
  padding: 1.7rem 2.4rem;
  font-size: 1.3rem;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
  transition: transform 0.2s;
  letter-spacing: 2.1px;
  text-align: center;
  backface-visibility: hidden;
}
.btn-primary {
  @extend %btn;
  color: #fff;
  background: #35cf94;
}
.btn-secondary {
  @extend %btn;
  color: $color-primary;
  background: #fff;
}
.btn-primary,
.btn-secondary {
  &:hover {
    transform: translateY(-0.3rem);
  }
  &:active {
    transform: translateY(-0.1rem);
  }
}
.btn-tertiary {
  @extend %btn;
  color: #fff;
  background: transparent;
  box-shadow: none;
  border: 2px solid $color-secondary;
  transition: all 0.2s;

  &:hover {
    color: $color-primary;
    background-color: #fff;
    border-color: $color-secondary;
  }
}
.btn-frame {
  @extend %btn;
  color: $color-primary;
  background-color: #fff;
  box-shadow: inset 0 0 0 2px $color-primary;
  text-shadow: 0 1px 0 #fff;
  transition: box-shadow 0.2s;
  &--light {
    box-shadow: inset 0 0 0 2px #e4ecf4;
    &:hover {
      box-shadow: inset 0 0 0 2px $color-primary;
    }
  }
  &:hover {
    box-shadow: inset 0 0 0 2px #ccc;
  }
}
.btn-sm {
  padding: 12px 15px;
  font-size: 1rem;
  letter-spacing: 1.6px;
}
.container {
  max-width: 1278px;
  padding: 0 15px;
  margin: 0 auto;
}
.content-box {
  max-width: 384px;
  margin: 0 auto;
  text-align: center;
}
.content-box-wider {
  max-width: 43rem;
}
