.fundraise {
  background-color: #f8f4f5;
  &__inner {
    display: flex;
    align-items: center;
  }
  &__content {
    max-width: 38.4rem;
    margin-left: 32rem;
    margin-right: 3rem;
  }
  &__bg {
    background-image: url("../../img/fundraise_backgorund.jpg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    min-height: 60rem;
    width: 50%;
  }
}
